<template>
  <div id="app">
    <v-app>
      <v-main>
        <div style="display:flex; width:100%" v-if="ready">
          <vue-tabs-chrome theme="custom" ref="tab" v-model="active_tab" :tabs="tabs" :gap="0" :autoHiddenCloseIconWidth="170"
            :onClose="CloseTab" :tabCloseWidth="50">
            <button slot="after" @click="AddTab" :disabled="loading" class="add-button fluent-icon " style="display:flex; 
            align-items: center;
    justify-content: center;"> 
              <template v-if="loading">
                <v-progress-circular
                :size="18"
                :width="2"
                color="grey"
                indeterminate
              ></v-progress-circular>
              </template>
              <template v-else>
                &#xE710;
              </template>
            </button>
            <template #progress="{ tab}">
              <v-progress-linear style="position: absolute;" stream buffer-value="0" height="2" :value="(tab.character.done.length / tab.character.routines.length) * 100 " :color="active_tab  == tab.character.nickname ? 'neon' : 'grey'"></v-progress-linear>
            </template>
          </vue-tabs-chrome>
        </div>
      </v-main>
    </v-app>

  </div>
</template>
<script>
// import VueTabsChrome from 'vue-tabs-chrome'
import VueTabsChrome from './packages'
import "@/assets/style.scss"
import axios from 'axios'
const procedure = async (data) => {
  try {
    await window.chrome.webview.postMessage(data)
  }
  catch (ex) {
    console.log(ex)
  }
}
const clone = (obj) => JSON.parse(JSON.stringify(obj))

const SERVER_ADDR = "http://192.168.1.99:3000"
const sleep = (s) => new Promise((resolve) => {
  setTimeout(() => {
    resolve()
  },s)
})
export default {
  components: {
    VueTabsChrome
  },
  data: () => ({
    active_tab: "",
    tabs: [],
    ready: false,
    workspace_mode: new URL(window.location).searchParams.get("workspace_mode"),
    loading: false


  }),
  watch: {
    active_tab(new_key, old_key) {
      let tab = this.tabs.find((tab) => tab.character.nickname == new_key)
      if (tab !== undefined && old_key !== new_key) procedure({ Event: 'focus_tab', Data: tab.character.nickname })
    },
  },
  methods: {
    CloseTab(e) {
      const characters = this.tabs
      axios.post(`${SERVER_ADDR}/character/${e.character.nickname}/offline`)
      procedure({ Event: 'close_tab', Data: e.character.nickname })
      console.log("CloseCharacter", e.character, e)
      return true
    },
    CloseAll(){
      const characters = clone(this.tabs).map((t) => {
        let nickname =  t.character.nickname
        this.$refs.tab.removeTab(nickname)
        return  t.character.nickname
      }).join(",")

      axios.post(`${SERVER_ADDR}/character/${characters}/offline`)
    },
    CloseTabByRemote(nickname) {
      this.$refs.tab.removeTab(nickname)
    },
    async AddTab() {
      if (this.workspace_mode == "freemode") procedure({ Event: 'select_character' })
      else if (this.workspace_mode == "automate") { 
        while(this.loading)    await sleep(1000)
        
        this.loading = true
       
        axios.get(`${SERVER_ADDR}/character/auto_fetch`).then((res) => {
          console.log(res)
          this.LoadTab(res.data.data)
        
          procedure({ Event: 'load_character', Data:res.data.data })
        }).catch(ex => {
          console.log(ex)
        }) .finally(() => {
          this.loading = false
        })
      } 
      // procedure({ Event: "fetch_character" })
    },
    SyncTabs(tabs_cfg) {
      try {
        console.log(tabs_cfg)
        var tabs = JSON.parse(tabs_cfg).map((character) => ({
          label: character.nickname,
          key: character.nickname,
          character,
          dragable: false,
          favicon: () => { }
        }))

        console.log(tabs)
        this.tabs = tabs

        console.log(this.tabs)

      } catch (ex) {
        console.log(ex)
      } finally {
        setTimeout(() => {
          this.ready = true
        }, 300)
      }

    },

    SetActiveTab(active_tab) {
      this.active_tab = active_tab
      console.log(this.active_tab)
    },

    LoadTab(character) {
      let tab = this.tabs.find((tab) => tab.character.nickname == character.nickname)
      console.log("mounting", tab, tab == undefined, character)
      if (tab == undefined) {
        if (this.$refs.tab) {
          this.$refs.tab.addTab({
            label: character.nickname,
            key: character.nickname,
            character,
            dragable: false,
            favicon: () => { }
          })
        }
        if (this.tabs.length == 1 || this.workspace_mode == 'freemode') {
          setTimeout(() => {
            this.active_tab = character.nickname
          }, 100)
        }
      }
    },
    ChangeProgress({nickname, done}){
      let tab = this.tabs.find((tab) => tab.character.nickname == nickname)
      if (tab) {
        tab.character.done = done
      }
    }
    
  },
  mounted() {
    window.$com = this
    if(process.env.NODE_ENV == "development") { 
      this.ready = true
      this.workspace_mode = "automate"
    }
    // this.ready = true
    // setTimeout(() => {
    //   this.LoadTab({ nickname: "Arika", progress : 20 })
    //   this.LoadTab({ nickname: "Arka", progress : 50 })
    // }, 200)

  }
}
</script>
